import { Component, EventEmitter, Input, Output, signal, inject, OnDestroy, ViewChild } from '@angular/core';
import { Item, Contact } from '../../../models/partner.model';
import { ConfirmationModalComponent, ConfirmationModalData } from '../../shared/confirmation-modal/confirmation-modal.component';

interface ContactApiResponse {
  success: boolean;
  message: string;
  data: Contact;
  errors: any[];
}
import { PartnersService } from '../../../services/partners.service';
import { TranslationService } from '../../../services/translation.service';

@Component({
  selector: 'app-view-partner-modal',
  standalone: false,
  templateUrl: './view-partner-modal.component.html',
  styleUrl: './view-partner-modal.component.css'
})
export class ViewPartnerModal implements OnDestroy {
  @Input() partner: Item | null = null;
  @Output() contactsUpdated = new EventEmitter<number>();
  @Output() partnerUpdated = new EventEmitter<Item>();
  @ViewChild('confirmationModal') confirmationModal!: ConfirmationModalComponent;

  private partnersService = inject(PartnersService);
  private translationService = inject(TranslationService);

  // UI State
  showModal = signal(false);
  loading = signal(false);
  editingContact = signal<Contact | null>(null);
  showAddContactForm = signal(false);
  newContact = signal<Partial<Contact>>({
    email: '',
    phone: '',
    mobile: ''
  });

  // Contact form validation
  contactFormValid = signal(false);
  private validationTimeout: any = null;
  
  // Contact to be deleted
  private contactToDelete: Contact | null = null;

  open(partner: Item) {
    this.partner = partner;
    this.showModal.set(true);
    
    // Only load contacts if they're not already present or if the array is empty
    if (!partner.contacts || partner.contacts.length === 0) {
      this.loadContacts();
    }
  }

  close() {
    this.showModal.set(false);
    this.partner = null;
    this.editingContact.set(null);
    this.showAddContactForm.set(false);
    this.resetNewContact();
  }

  private loadContacts() {
    if (!this.partner) return;

    console.log('Loading contacts for partner:', this.partner.id);
    this.loading.set(true);
    this.partnersService.getContacts(this.partner.id).subscribe({
      next: (contacts) => {
        console.log('Contacts loaded:', contacts);
        if (this.partner) {
          this.partner.contacts = contacts || [];
        }
        this.loading.set(false);
      },
      error: (error) => {
        console.error('Error loading contacts:', error);
        if (this.partner) {
          this.partner.contacts = [];
        }
        this.loading.set(false);
      }
    });
  }

  // Contact Management Methods
  addContact() {
    if (!this.partner || !this.contactFormValid()) return;

    const contactData = this.newContact();
    console.log('Adding contact:', contactData, 'for partner:', this.partner.id);
    this.loading.set(true);

    this.partnersService.addContact(this.partner.id, contactData as Contact).subscribe({
      next: (response: ContactApiResponse) => {
        console.log('Contact added successfully:', response);
        if (response.success && response.data && this.partner) {
          // Ensure contacts array exists
          if (!this.partner.contacts) {
            this.partner.contacts = [];
          }
          this.partner.contacts.push(response.data);
          // Force change detection by creating a new array reference
          this.partner.contacts = [...this.partner.contacts];
          this.forceUIUpdate();
        } else {
          console.error('Failed to add contact:', response.message);
        }
        this.resetNewContact();
        this.showAddContactForm.set(false);
        this.loading.set(false);
        this.contactsUpdated.emit(this.partner?.id);
      },
      error: (error) => {
        console.error('Error adding contact:', error);
        this.loading.set(false);
      }
    });
  }

  editContact(contact: Contact) {
    this.editingContact.set({ ...contact });
    this.showAddContactForm.set(false);
  }

  updateContact() {
    if (!this.partner || !this.editingContact()) return;

    const contact = this.editingContact()!;
    console.log('Updating contact:', contact, 'for partner:', this.partner.id);
    this.loading.set(true);

    this.partnersService.updateContact(this.partner.id, contact.id, contact).subscribe({
      next: (response: ContactApiResponse) => {
        console.log('Contact updated successfully:', response);
        if (response.success && response.data && this.partner) {
          const index = this.partner.contacts.findIndex(c => c.id === contact.id);
          if (index !== -1) {
            this.partner.contacts[index] = response.data;
            // Force change detection by creating a new array reference
            this.partner.contacts = [...this.partner.contacts];
            this.forceUIUpdate();
          }
        } else {
          console.error('Failed to update contact:', response.message);
        }
        this.editingContact.set(null);
        this.loading.set(false);
        this.contactsUpdated.emit(this.partner?.id);
      },
      error: (error) => {
        console.error('Error updating contact:', error);
        this.loading.set(false);
      }
    });
  }

  deleteContact(contact: Contact) {
    if (!this.partner) return;

    this.contactToDelete = contact;
    const modalData: ConfirmationModalData = {
      title: this.translate('partners.delete_contact'),
      message: this.translate('partners.confirm_delete_contact'),
      confirmText: this.translate('partners.delete'),
      cancelText: this.translate('partners.cancel'),
      type: 'delete'
    };
    
    this.confirmationModal.show(modalData);
  }

  onConfirmDelete() {
    if (!this.partner || !this.contactToDelete) return;

    console.log('Deleting contact:', this.contactToDelete, 'for partner:', this.partner.id);
    this.loading.set(true);

    this.partnersService.deleteContact(this.partner.id, this.contactToDelete.id).subscribe({
      next: () => {
        console.log('Contact deleted successfully');
        if (this.partner) {
          this.partner.contacts = this.partner.contacts.filter(c => c.id !== this.contactToDelete!.id);
          // Force change detection by creating a new array reference
          this.partner.contacts = [...this.partner.contacts];
          this.forceUIUpdate();
        }
        this.loading.set(false);
        this.contactsUpdated.emit(this.partner?.id);
        this.contactToDelete = null;
      },
      error: (error) => {
        console.error('Error deleting contact:', error);
        this.loading.set(false);
        this.contactToDelete = null;
      }
    });
  }

  onCancelDelete() {
    this.contactToDelete = null;
  }

  cancelEdit() {
    this.editingContact.set(null);
    this.showAddContactForm.set(false);
    this.resetNewContact();
  }

  private resetNewContact() {
    this.newContact.set({
      email: '',
      phone: '',
      mobile: ''
    });
    this.validateContactForm();
  }

  validateContactForm() {
    const contact = this.editingContact() || this.newContact();
    const email = contact.email?.trim();
    const phone = contact.phone?.trim();
    const mobile = contact.mobile?.trim();
    const isValid = !!(email && (phone || mobile));
    
    // Only update if the validation state actually changed
    if (this.contactFormValid() !== isValid) {
      this.contactFormValid.set(isValid);
    }
  }

  onContactFieldChange() {
    this.debouncedValidation();
  }

  onEditingContactFieldChange(field: keyof Contact, value: string) {
    const contact = this.editingContact();
    if (contact) {
      (contact as any)[field] = value;
      this.debouncedValidation();
    }
  }

  private debouncedValidation() {
    if (this.validationTimeout) {
      clearTimeout(this.validationTimeout);
    }
    this.validationTimeout = setTimeout(() => {
      this.validateContactForm();
    }, 150);
  }

  private forceUIUpdate() {
    // Force change detection by updating the partner reference
    if (this.partner) {
      this.partner = { ...this.partner };
    }
  }

  getCategoryLabel(category: number): string {
    const categories: Record<number, string> = {
      0: this.translate('partners.categories.0'),
      1: this.translate('partners.categories.1'),
      2: this.translate('partners.categories.2'),
      3: this.translate('partners.categories.3'),
      4: this.translate('partners.categories.4'),
      5: this.translate('partners.categories.5')
    };
    return categories[category] || 'Unknown';
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  ngOnDestroy() {
    if (this.validationTimeout) {
      clearTimeout(this.validationTimeout);
    }
  }
}
